import * as React from 'react';
import {Link} from 'gatsby';
import Img from 'gatsby-image';
import {
    CollectionCardWrapper,
    CollectionContent,
    CollectionItemPreview,
    CollectionDetails,
    CollectionPreview,
    CollectionTitle,
    Excerpt,
    ReadMore,
} from './collection-card-minimal.style';
import {PostTags} from "../post-card-minimal/post-card-minimal.style";
import _ from "lodash";

interface CollectionCardMinimalProps {
    image?: any;
    title: string;
    description?: string;
    url: string;
    date?: string;
    collection?: string;
    tags?: [];
    className?: string;
    imageType?: 'fixed' | 'fluid';
}

const CollectionCardMinimal: React.FunctionComponent<CollectionCardMinimalProps> = (
    {
        image,
        title,
        description,
        url,
        date,
        collection,
        tags,
        className,
        imageType,
        ...props
    }
) => {
    // Add all classs to an array
    const addAllClasses = ['collection_card'];

    // className prop checking
    if (className) {
        addAllClasses.push(className);
    }

    return (
        <CollectionCardWrapper className={addAllClasses.join(' ')} {...props}>
            <CollectionDetails className="collection_details">
                <CollectionItemPreview>
                    {image == null ? null : (
                        <CollectionPreview className="collection_preview">
                            <Link to={url}>
                                {imageType === 'fluid' ? (
                                    <Img fluid={image} alt="collection preview"/>
                                ) : (
                                    <Img fixed={image} alt="collection preview"/>
                                )}
                            </Link>
                        </CollectionPreview>
                    )}
                </CollectionItemPreview>

                <CollectionContent className="collection_content">
                    {tags == null ? null : (
                        <PostTags className="collection_tags">
                            {tags.map((tag: string, index: number) => (
                                <Link key={index} to={`/tags/${_.kebabCase(tag)}/`}>
                                    {`#${tag}`}
                                </Link>
                            ))}
                        </PostTags>
                    )}
                    <CollectionTitle className="collection_title">
                        <Link to={url}>{title}</Link>
                    </CollectionTitle>
                    {description && (
                        <Excerpt
                            dangerouslySetInnerHTML={{
                                __html: description,
                            }}
                            className="excerpt"
                        />
                    )}
                    <ReadMore>
                        <Link to={url}>Read More</Link>
                    </ReadMore>
                </CollectionContent>
            </CollectionDetails>
        </CollectionCardWrapper>
    );
};

CollectionCardMinimal.defaultProps = {
    imageType: 'fluid',
};

export default CollectionCardMinimal;
