import React from 'react';
import {graphql} from 'gatsby';
import Layout from '../components/layout';
import SEO from '../components/seo';
import {
    CollectionDescription,
    CollectionHeroImage,
    CollectionName,
    CollectionPageHeading,
    CollectionPostsWrapper
} from './templates.style';
import {collectionKnowledge} from '../../content/collection/knowledge';
import CollectionCardMinimal from "../components/collection-card-minimal/collection-card-minimal";
import Image from "gatsby-image";

const Collections = ({pageContext, data}: any) => {
    const {collection} = pageContext;
    const {edges, totalCount} = data.allMarkdownRemark;

    const collectionInfo = collectionKnowledge[collection];

    return (
        <Layout>
            <SEO title={collectionInfo.title} description={`A collection of ${totalCount} post`}/>
            <CollectionHeroImage>
                <Image fluid={data[`${collection}_hero`].childImageSharp.fluid} alt="author"/>
            </CollectionHeroImage>
            <CollectionPostsWrapper>
                <CollectionPageHeading>
                    <CollectionName>{collectionInfo.title}</CollectionName>
                    <CollectionDescription>{collectionInfo.description}</CollectionDescription>
                    {`This collection has ${totalCount} ${totalCount > 1 ? "posts" : "post"}`}
                </CollectionPageHeading>
                {edges.map(({node}: any) => {
                    const title = node.frontmatter.title || node.fields.slug;
                    return (
                        <CollectionCardMinimal
                            key={node.fields.slug}
                            title={title}
                            image={
                                node.frontmatter.featuredImg.childImageSharp.fluid
                            }
                            url={node.fields.slug}
                            description={node.frontmatter.description || node.excerpt}
                            date={node.frontmatter.date}
                            tags={node.frontmatter.tags}
                            collection={node.frontmatter.collection}
                        />
                    );
                })}
            </CollectionPostsWrapper>
        </Layout>
    );
};

export default Collections;

export const imageHeroFragment = graphql`
    fragment ImageHeroFragment on File {
        childImageSharp {
            fluid(maxWidth: 1770, quality: 90) {
                ...GatsbyImageSharpFluid
            }
        }
    }
`

export const pageQuery = graphql`
    query CollectionsQuery($collection: String) {
        allMarkdownRemark(
            limit: 500
            sort: { fields: [frontmatter___date], order: DESC }
            filter: { frontmatter: { collection: { in: [$collection] } } }
        ) {
            totalCount
            edges {
                node {
                    excerpt(pruneLength: 300)
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "DD [<span>] MMMM [</span>]")
                        title
                        description
                        collection
                        tags
                        featuredImg {
                            childImageSharp {
                                fluid(maxWidth: 325, maxHeight: 325, quality: 90) {
                                    ...GatsbyImageSharpFluid_withWebp_tracedSVG
                                }
                            }
                        }
                    }
                }
            }
        }
        SSAN1_hero: file(relativePath: { eq: "SSAN1/hero.jpg" }) {
            ...ImageHeroFragment
        }
        SDSP1_hero: file(relativePath: { eq: "SDSP1/hero.jpg" }) {
            ...ImageHeroFragment
        }
    }
`;
